import {
  ProjectReportId,
  ReportText,
  ReportTypeCode,
  Title,
  WorkerId,
} from '../value';

import {
  BaseProjectReport,
  BaseUpdateProjectReport,
  IBaseProjectReport,
  IUpdateBaseProjectReport,
} from './BaseProjectReport';

export interface IProjectWorkerReport extends IBaseProjectReport {
  title: Title;
  reportText: ReportText;
  reportTypeCode: ReportTypeCode;
  workerIds: WorkerId[];
}
export interface IUpdateProjectWorkerReport extends IUpdateBaseProjectReport {
  title?: Title;
  reportText?: ReportText;
  reportTypeCode?: ReportTypeCode;
  workerIds?: WorkerId[];
}

export class ProjectWorkerReport extends BaseProjectReport {
  readonly title: Title;
  readonly reportText: ReportText;
  readonly reportTypeCode: ReportTypeCode;
  readonly workerIds: WorkerId[];
  constructor(args: IProjectWorkerReport) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.reportTypeCode = args.reportTypeCode;
    this.workerIds = args.workerIds;
  }

  update(args: IUpdateProjectWorkerReport): UpdateProjectWorkerReport {
    return new UpdateProjectWorkerReport({ id: this.id, ...args });
  }
}

export class UpdateProjectWorkerReport extends BaseUpdateProjectReport {
  readonly title?: Title;
  readonly reportText?: ReportText;
  readonly reportTypeCode?: ReportTypeCode;
  readonly workerIds?: WorkerId[];
  constructor(args: IUpdateProjectWorkerReport & { id: ProjectReportId }) {
    super(args);
    this.title = args.title;
    this.reportText = args.reportText;
    this.reportTypeCode = args.reportTypeCode;
    this.workerIds = args.workerIds;
  }
}
